@font-face {
    font-family: DigimonBasic;
    src: url('../Letras/DigimonBasic.ttf') format('truetype');
}

.carousel{
    position: absolute;
    top: 11vw;
    left: 16vw;
    border-radius: 10px;
}

.imagTaiga {
    width: 60vw;
    border-radius: 10px;
    cursor: pointer;
}

.titleTaiga {
    background-color: cadetblue;
    font-family: DigimonBasic;
    color: black;
}

.pTaiga {
    background-color: yellow;
    font-family: DigimonBasic;
    color: black;
}

