@font-face {
    font-family: DigimonBasic;
    src: url('../Letras/DigimonBasic.ttf') format('truetype');
}

.ajusteFondo {
    width: 99vw;
    height: 100vw;
}

.conatiner {
    position: absolute;
    background-color: rgba(25, 167, 25, 0.767);
    top: 130px;
    width: 100vw;
    height: 55px;
    left: 50%;
    transform: translateX(-50%);
}

.titulo {
    font-family: DigimonBasic;
    font-size: 50px;

}

.kunemon {
    position: absolute;
    top: 11.5%;
    width: 45px;
    left: 5vw;
    cursor: pointer;
}

.kunemon:active{
    animation: normal;
    animation-duration: 0.5s;
    animation-name: rebote;
    animation-iteration-count: 4;
    animation-direction: alternate;
}

@keyframes rebote{
    from{
        margin-bottom: 10%;
    }to{
        margin-top: -1%;
    }
}

